.ArticlePage-container {
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex: 1;
  margin: 1vw;
  padding: 1vw;
  border: 1px solid white;
  border-radius: 3px;
  min-height: 82vh;
}

.ArticlePage-LoadingImage {
  position: absolute;
  align-self: center;
  left: 48vw;
}

code:not([class]) {
  display: inline-block;
  overflow: auto;
  scrollbar-width: thin;
  max-width: 95vw;
  padding: 1vh;
  font-size: 90%;
  background-color: rgb(0, 0, 0, 0.5);
  border-radius: 3px;
}

img:not([class]) {
  max-width: 97vw;
  max-height: 95vh;
}

h1:not([class]) {
  text-align: center;
}

p:not([class]) {
  min-width: 96vw;
  display: flex;
  justify-content: center;
}
